<template>
  <div id='advice' @touchmove='cancelInput' @touchend="cancelInput">
    <header>
      <div id='back' @click="back">
        <img id='back-pic' src='static/assets/back.png' alt='back'>
      </div>
      <p>反馈</p>
    </header>
    <div v-show='!success' id='question'>
      <div class='question-item'>
        <p>{{list[0]}}</p>
        <p class='prompt'>{{prompt}}</p>
        <div v-for='i in 5' class="star-box">
          <img class='star' alt='star' @touchstart='grade(i)'>
        </div>
      </div>
      <div class='question-item'>
        <p>{{list[1]}}</p>
        <textarea class='comment' placeholder='请把您发现的问题告诉我们哦，我们将会在后续的版本中修复该问题，谢谢！' @focus='easywrite'></textarea>
      </div>
      <div class='question-item'>
        <p>{{list[2]}}</p>
        <textarea class='comment' placeholder='请把您希望添加的功能告诉我们哦，感谢您宝贵的建议，我们将会不断改进！' @focus='easywrite'></textarea>
      </div>
      <input id='submit' type='submit' @click.prevent="submit">
    </div>
    <img v-show='success' id='success' src="static/assets/success.png" alt="success">
    <transition name="prompt"><p v-show="success" id="prompt">提交成功</p></transition>
    <transition name="check"><p v-show="success" id="thanks">感谢您的反馈</p></transition>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name: 'advice',
    data () {
      return {
        list: [
          '综合评分',
          '问题反馈',
          '您希望添加什么功能'
        ],
        promptList: [
          '太棒了',
          '挺不错',
          '还可以',
          '不好用',
          '太差了'
        ],
        prompt: '',
        success: false,
        cancelTag: -1
      }
    },
    methods: {
      back: function () {
        this.$router.push({path: '/mine'})
      },
      submit: function () {
        var self = this
        this.success = true
        setTimeout(function () {
          self.back()
        }, 2800)
      },
      grade: function (i) {
        event.preventDefault()
        event.stopPropagation()
        for (var j = 4; j >= 0; j--) {
          if (j >= i - 1) {
            $('.star').eq(j).attr('src', 'static/assets/star.png')
          } else {
            $('.star').eq(j).attr('src', 'static/assets/star-grey.png')
          }
        }
        this.prompt = this.promptList[i - 1]
      },
      easywrite: function (ev) {
        var index = $('textarea').index(ev.target)
        // console.log('in')
        // console.log('previous:' + this.cancelTag)
        // console.log('index:' + index)
        if (index >= 0 && index <= 1) {
          var temp = $('textarea').eq(index).offset().top - 270
          if (this.cancelTag !== -1) {
            temp += 160
          }
          $('#question').css('-webkit-transform', 'translateY(-' + temp + 'px)')
          $('#question').css('transform', 'translateY(-' + temp + 'px)')
        }
        this.cancelTag = index
      },
      cancelInput: function (ev) {
        var index = $('textarea').index(ev.target)
        if (index === -1) {
          // console.log('out')
          // console.log('previous:' + this.cancelTag)
          // console.log('index:' + index)
          if (this.cancelTag !== index && this.cancelTag !== -1) {
            $('textarea').eq(this.cancelTag).blur()
          }
          if (this.cancelTag >= 0 && this.cancelTag <= 1) {
            $('#question').css('-webkit-transform', 'none')
            $('#question').css('transform', 'none')
          }
          this.cancelTag = -1
        }
      }
    },
    mounted () {
      $('.star').attr('src', 'static/assets/star-grey.png')
    }
  }
</script>

<style scoped>
  #advice{
    position: absolute;
    width: 100%;
    height: 100%;
    overflow: scroll;
    background: white;
    z-index: 4;
  }
  header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 140px;
    background-color: #1296db;
    z-index: 3;
  }
  header p{
    font-size: 66px;
    line-height: 140px;
    color: white;
    float: left;
    margin: 0 18%;
  }
  #back{
    width: 14%;
    height: 140px;
    position: fixed;
    top: 0;
    left: 4%;
    z-index: 2;
    overflow: hidden;
  }
  #back img{
    width: 60px;
    margin-left: 50%;
    margin-top: 50%;
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    padding: 0;
  }
  #question{
    width: 84%;
    height: auto;
    margin: 140px 8% 0;
    padding: 20px 0;
    z-index: 1;
  }
  .question-item:not(:first-of-type){
    width: 100%;
    height: auto;
    padding: 40px 0;
    overflow-y: hidden;
  }
  .question-item:not(:last-of-type){
    border-bottom: 3px solid #f0f2f5;
  }
  .question-item p:not(.prompt){
    font-size: 48px;
    font-weight: bold;
    float: left;
  }
  .question-item:first-of-type p{
    margin: 40px 0;
  }
  .question-item:first-of-type{
    width: 100%;
    height: 136px;
    overflow: hidden;
  }
  .prompt{
    font-size: 44px;
    float: left;
    padding: 4px 0 8px 16px;
  }
  .star-box{
    width: auto;
    height: 56px;
    float: right;
    position: relative;
    bottom: 8px;
    padding: 40px 4px 40px 4px;
  }
  .star-box img{
    width: 60px;
  }
  .comment{
    width: calc(100% - 40px);
    height: 280px;
    margin-top: 40px;
    outline: none;
    resize: none;
    background-color: #F8F8F8;
    border: 3px solid #F0F2F5;
    border-radius: 8px;
    font-size: 40px;
    line-height: 140%;
    padding: 12px 16px;
  }
  .comment:focus{
    border: 3px solid #1296db;
  }
  .comment::-moz-selection{
    color: black;
    background-color: #DBDBDB;
  }
  .comment::selection{
    color: black;
    background-color: #DBDBDB;
  }
  #submit{
    width: 300px;
    height: 95px;
    margin: 20px calc((100% - 300px) / 2) 50px;
    border: none;
    border-radius: 12px;
    outline: none;
    background-color: #1296db;
    color: white;
    font-size: 44px;
  }
  #success{
    width: 200px;
    position: absolute;
    top: calc((100% - 400px) / 2);
    left: calc((100% - 200px) / 2);
    -webkit-animation: up .4s;
    -moz-animation: up .4s;
    -o-animation: up .4s;
    animation: up .4s;
    -webkit-transform: translate3d(0, 0, 0);
    -moz-transform: translate3d(0, 0, 0);
    -ms-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
  @-webkit-keyframes up {
    0%{
      opacity: 0;
      -webkit-transform: translateY(80px);
    }
    100%{
      opacity: 1;
      -webkit-transform: translateY(0);
    }
  }
  @-moz-keyframes up {
    0%{
      opacity: 0;
      -moz-transform: translateY(80px);
    }
    100%{
      opacity: 1;
      -moz-transform: translateY(0);
    }
  }
  @-o-keyframes up {
    0%{
      opacity: 0;
      -o-transform: translateY(80px);
    }
    100%{
      opacity: 1;
      -o-transform: translateY(0);
    }
  }
  @keyframes up {
    0%{
      opacity: 0;
      transform: translateY(80px);
    }
    100%{
      opacity: 1;
      transform: translateY(0);
    }
  }
  #prompt{
    width: 100%;
    text-align: center;
    font-size: 48px;
    position: absolute;
    top: calc(50% + 25px);
  }
  .prompt-enter-active{
    -webkit-animation: text .6s;
    -moz-animation: text .6s;
    -o-animation: text .6s;
    animation: text .6s;
  }
  @-webkit-keyframes text {
    0%, 66.6%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
  }
  @-moz-keyframes text {
    0%, 66.6%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
  }
  @-o-keyframes text {
    0%, 66.6%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
  }
  @keyframes text {
    0%, 66.6%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
  }
  #thanks{
    width: 100%;
    text-align: center;
    font-size: 46px;
    position: absolute;
    top: calc(50% + 100px);
    color: black;
    text-decoration: none;
  }
  .check-enter-active{
    -webkit-animation: text2 0.8s;
    -moz-animation: text2 0.8s;
    -o-animation: text2 0.8s;
    animation: text2 0.8s;
  }
  @-webkit-keyframes text2 {
    0%, 75%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
  }
  @-moz-keyframes text2 {
    0%, 75%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
  }
  @-o-keyframes text2 {
    0%, 75%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
  }
  @keyframes text2 {
    0%, 75%{
      opacity: 0;
    }
    100%{
      opacity: 1;
    }
  }
</style>



